@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        height: 25rem;
        background-image: url(../img/principal-festejos.jpg);
        background-size: cover;
        background-position: center center;
        position: relative;
        @include m.tablet {
            height: auto;
        }
    }

    .imagen-texto {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        bottom: 0;
        margin-left: 1rem;
        @include m.tablet {
            margin: 0 0 10rem 3rem;
        }

        h1 {
            font-family: v.$fuenteDecorativa;
            color: v.$rosa;
            font-size: 7rem;
            margin: 0;
            @include m.desktop {
                font-size: 9rem;
            }
            @include m.FHD {
                font-size: 12rem;
            }
            @include m.QHD {
                font-size: 15rem;
            }
        }

        p {
            font-family: v.$fuenteDecorativa;
            color: darken(v.$azul, 30%);
            margin: 0;
            font-size: 3rem;
            @include m.desktop {
                font-size: 4.5rem;
            }
            @include m.FHD {
                font-size: 6rem;
            }
            @include m.QHD {
                font-size: 9rem;
            }
        }
    }

    .app {
        width: 95%;
        margin: 0 auto;
        @include m.tablet {
            width: 100%;
            padding: 0 2rem;
            overflow: scroll;
        }

        h2 {
            margin-bottom: 0;
        }
    }
}

.barra {
    margin: v.$separacion 0;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .boton {
        @include m.boton(v.$rosa, v.$negro);
        margin: 0;
    }
}

.barra-servicios {
    margin-bottom: v.$separacion;

    @include m.tablet {
        display:flex;
        justify-content:space-between;
        gap: 1rem;
    }

    .boton {
        margin: 0 0 1rem 0;
    }
}