@use "base/variables" as v;
@use "base/mixins" as m;

.opciones {
    margin-top: v.$separacion;
    @include m.tablet {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    a {
        color: v.$negro;
        display: block;
        margin-bottom: calc(v.$separacion/2);
         padding: 1rem;
        text-align: center;
        transition: all .2s ease-in-out;
        &:hover {
            background-color: v.$amarillo;
            border-radius: 1rem;
            transform: scale(1.1);
        }
    }
}