@use "base/variables" as v;

.reservas {
    list-style: none;
    padding: 0;
    li {
        padding: 1rem;
        border-top: 1px solid v.$negro;
        p {
            font-weight: v.$regular;
            color: v.$negro;
        }
        span,
        .servicio {
            font-weight: v.$bold;
            color: v.$negro;
        }
    }
}

.servicios {
    @extend .reservas;

    .acciones {
        display: flex;
        justify-content: space-between;
        margin: 0;
    }
    .boton,
    .boton-eliminar {
        margin: 0;
    }
}