@use "base/variables" as v;
@use "base/mixins" as m;

.listado-servicios {
    @include m.tablet {
        display: grid;
        gap: 2rem;
        @include m.grid(2, 2rem);
    }

    .servicio {
        
        background-color: v.$amarillo;
        padding: 2rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 3000ms;
        text-align: center;
        margin-bottom: 2rem;
        transition: all .5s ease-in-out;

        &.seleccionado {
            background-color: darken(v.$amarillo, 10%);
            p {
                color: v.$negro;
            }

            .precio-servicio {
                color: v.$verde;
            }
        }

        @include m.tablet {
            margin-bottom: 0;
        }

        &:hover {
            transform: scale(1.1);
            cursor: pointer;
        }

        p {
            color: v.$negro;
            margin: 0 0 calc(v.$separacion/4) 0;
        }

        .precio-servicio {
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }
    }
}